.task-toolbar {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: var(--background-color);
  width: 100%;
  height: auto;
  border-top: 1px solid black;
}

.task-toolbar-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 200%;
}
